.modal-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -999;
  opacity: 0;
  transition: opacity 0.5s ease;
}
.modal-wrapper--isVisible {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 200;
  background-color: var(--bgc-underlay);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.modal-wrapper--isVisible> * {
  position: relative;
  z-index: 1;
}
.modal-wrapper--isSecondLayer {
  z-index: 300;
}
.modal-wrapper--isSecondLayer> * {
  position: relative;
  z-index: 1;
}
.modal-wrapper--isVisibleFullscreen {
  z-index: 400;
}
